@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  font-family: 'Mulish', sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.navbar{
  background-color: #fff !important;
  text-decoration: none;
}

.navbar-brand{
 font-size: 1.9rem !important;
 color: #565387;
 text-decoration: none;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
  text-decoration: none;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
  text-decoration: none;
}
.navbar a:hover{
  color:#3498db !important;
  text-decoration: none;
}
@media (max-width:991px){
  .navbar-brand{
    font-size: 1.5rem !important;
  }
}
.img-fluid-animated{
  width: 350px;
  height: 300px;
  margin-top: 50px;
}
#header{
  width: 100%;
  height:80vh ;
  margin-top: 70px;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 38px;
  font-weight: 700;
  color: black;
}
#header .brand-name{
  color: #3498db;

}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #3498db;
  border: 2px solid #3498db;
  text-decoration: none;
}
#header .btn-get-started:hover{
  background: #3498db;
  color: #fff;
}
.header-img{
  text-align: right;
}

#header .img-fluid-animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }

}
@keyframes up-down {
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }

}
.tab-center{
  text-align: center;
  font-weight: bolder;
  font-size: 50px;
  background: -webkit-linear-gradient(150deg,teal, white);
  color: maroon;
}
footer p {
  padding: 0;
  margin: 0;
}
@media (max-width : 991px){
  #header {
    height: 80vh;
    text-align: center;
}
  #header .header-img {
    text-align: center;
  }
  #header .header-img img {
    width:60%;
  }
}

@media (max-width : 768px){
  #header {
   margin-top: 20px;
}
  #header h1 {
  font-size: 28px;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width:70%;
  }
}